.datatable-crud .higher7percent {
    color: #ffffff;
    background-color: #2429c5 !important;
    width: fit-content;
}

.datatable-crud .higher20percent {
    color: #ffffff;
    background-color: #28630f !important;
    width: fit-content;
}

.datatable-crud .lower7percent {
    color: #ffffff;
    background-color: #d25b27 !important;
    width: fit-content;
}

.datatable-crud .lower20percent {
    color: #ffffff;
    background-color: #720707 !important;
    width: fit-content;
}

.money_style {
    color: #ffffff;
    background-color: #00b215 !important;
    width: fit-content;
}

.roi_style {
    color: #ffffff;
    background-color: #fbe26c !important;
    width: fit-content;
}

.datatable-crud .table-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media screen and (max-width: 960px) {
    .datatable-crud .table-header {
        align-items: flex-start;
    }
}

.datatable-crud .product-image {
    width: 100px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.p-dropdown {
    width: 7rem;
}

.datatable-crud .p-dialog .product-image {
    width: 150px;
    margin: 0 auto 2rem auto;
    display: block;
}

.datatable-crud .confirmation-content {
    display: flex;
    align-items: center;
    justify-content: center;
}

.datatable-column {
    min-width: 8rem;
}

.dialog-width {
    width: 450px;
}

.input-money-margin {
    margin-right: 10px;
}

.input-money-margin-span{
    display: flex;
    justify-content: center;
}

.glossary_text_box{
    position: absolute;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.5px;
    z-index: 100;
    background-color: #fff;
    width: 200px;
    padding: 10px 10px;
    box-shadow: 0 0 10px #ddd;
    display: none;
    top: 75px;
    /*left:300px;*/
    border: 1px #ddd solid;
    color: #333;
    text-align: left;
}

.p-button:hover + .glossary_text_box {
    display: block;
    color: red;
}

@media screen and (max-width: 960px) {
    .datatable-crud .p-toolbar {
        flex-wrap: wrap;
    }

    .datatable-crud .p-toolbar .p-button {
        margin-bottom: 0.25rem;
    }

    .datatable-crud .table-header {
        flex-direction: column;
    }

    .datatable-crud .table-header .p-input-icon-left, .datatable-crud .table-header input {
        width: 100%;
    }
}